@font-face {
  font-family: 'FuturaPT';
  src: url('../fonts/FuturaPT-Book.woff') format("woff");
}
@font-face {
  font-family: 'FuturaPT';
  src: url('../fonts/FuturaPT-Bold.woff') format("woff");
  font-weight: bold;
}
@font-face {
  font-family: 'FuturaPT';
  src: url('../fonts/FuturaPT-Medium.woff') format("woff");
  font-weight: 500;
}
@font-face {
  font-family: 'FuturaPT-Demi';
  src: url('../fonts/FuturaPT-Demi.woff') format("woff");
  font-weight: 500;
}
* {
  all: unset;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  flex-shrink: 0;
}
*::before,
*::after {
  box-sizing: border-box;
}
head,
script {
  display: none;
}
html {
  height: 100%;
  font-size: 1px;
}
@media (max-width: 1200px) {
  html {
    font-size: 0.8px;
  }
}
body {
  height: 100%;
  font-size: 16rem;
  font-family: FuturaPT;
  color: #37334d;
  line-height: 1.25;
  -webkit-hyphens: auto;
  -ms-hyphens: auto;
  hyphens: auto;
}
main {
  flex-grow: 1;
}
.row {
  flex-direction: row;
}
body header {
  box-shadow: 0 2px 4px 0 rgba(225, 225, 225, 0.5);
}
body footer {
  box-shadow: inset 0 1px 3px 0 rgba(190, 190, 190, 0.5);
}
body header,
body footer {
  padding-top: 30px;
  background-color: #ffffff;
}
body header .row > *,
body footer .row > * {
  flex-direction: row;
  align-items: center;
  margin-bottom: 30px;
}
body header nav,
body footer nav {
  justify-content: space-around;
  font-size: 20rem;
}
body header .number,
body footer .number {
  flex-direction: column;
  align-items: flex-end;
}
body header .number span,
body footer .number span {
  font-family: FuturaPT-Demi;
  font-size: 28rem;
  line-height: 0.86;
  color: #3140ae;
  margin-bottom: 0.5em;
}
body header .number .recall,
body footer .number .recall {
  font-size: 16rem;
  color: #6b6b6b;
  text-decoration: underline;
}
body section {
  padding-top: 60rem;
  padding-bottom: 60rem;
}
h2 {
  text-align: center;
  font-weight: bold;
  font-size: 36rem;
  margin-bottom: 30rem;
}
h3 {
  font-family: FuturaPT-Demi;
  font-size: 26rem;
}
.btn {
  align-items: center;
  padding: 17rem 40rem;
  border-radius: 30px;
  cursor: pointer;
  background-image: linear-gradient(to top, #2948ff, #396afc);
  color: white;
  font-weight: bold;
}
.btn:hover,
.btn:active,
.btn:focus {
  background-image: linear-gradient(to top, #041ca9, #023cea);
}
.remark {
  color: #a1a1a1;
}
.plus {
  flex-direction: row;
  font-family: FuturaPT-Demi;
  font-size: 20rem;
}
.plus::before {
  content: '';
  height: 15px;
  width: 15px;
  background-image: url(../img/plus.svg);
  margin-right: 5px;
}
.download {
  flex-direction: row;
  margin-bottom: 30rem;
}
.download .remark {
  align-self: center;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}
.features {
  margin-bottom: 30rem;
}
.features li {
  font-size: 16rem;
  font-weight: 500;
}
.features li::before {
  content: '';
  width: 35px;
  height: 35px;
  margin-bottom: 10px;
}
.features li.earn::before {
  background-image: url(../img/earn.svg);
}
.features li.earn::before {
  background-image: url(../img/earn.svg);
}
.features li.earn::before {
  background-image: url(../img/earn.svg);
}
.clients {
  background-color: #f7faff;
}
#about li {
  align-items: center;
  margin-bottom: 50rem;
}
#about .left {
  text-align: right;
  font-size: 70rem;
  font-weight: bold;
  color: #3742fa;
}
@media (max-width: 1200px) {
  #about .left {
    font-size: 50rem;
  }
}
#about .right {
  max-width: 285px;
  text-align: left;
  font-size: 24rem;
}
#cities {
  font-size: 24rem;
}
.mask {
  position: relative;
  padding-bottom: 56.25%;
  overflow: hidden;
  border-radius: 15px;
  box-shadow: 0 2px 4px 0 rgba(190, 190, 190, 0.5);
  background-color: #f7faff;
  margin-bottom: 30rem;
}
.mask > * {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
